
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

/*body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #ccc;
}*/

.background-green {
  position: absolute;
  top: 0;
  width: 100%;
  height: 20%;
  background-color: #009688;
}

.main-container {
  position: relative;
  width: 100%;
  max-width:100%;
  height:89.3vh ;
  margin-top: 3.89rem;
  background: #fff;
  display: flex;
  box-shadow: 0px 1px 1px  0 rgba(0,0,0,0.5), 0px 2px 5px 0 rgba(0,0,0,0.6);
}

.main-container .left-container {
  position:relative;
  width: 20%;
  height:100%;
  flex: 30%;
  background: #fff;
}
.react-input-emoji--wrapper{
  border-radius: 0px;
  background:var(--color-black);
  color:var(--color-name);
}
.main-container .right-container {
  position: relative;
  width: 70%;
  height:100%;
  flex: 70%;
  background: var(--color-light);
}

.main-container .right-container::before {
  content: '';
  position: absolute;
  top: 0;
  background-repeat: no-repeat;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../assets/bg.png");
  opacity: 0.5;
}

.header {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  height: 60px;
  background:var(--color-light);
  color: var(--color-name);
  padding: 0 15px;

}

.user-img {
  position:relative;
  width: 40px;
  height: 40px;
  overflow: hidden;
  border-radius: 50%;
}
.user-img img{
  position: absolute;
  width: 100%;
  height: 100%;
}
.dp {
  position:absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
}

.nav-icons {
  display:flex;
  justify-content: flex-end;
  padding-left: 110px;
}

.nav-icons li {
  backgroud-color:pink;
  list-style: none;
  display: flex;
  cursor: pointer;
  color: #51585c;
  margin-left: 22px;
  font-size: 18px;
}

.notif-box {
  position: relative;
  display: flex;
  width: 100%;
  height: 70px;
  background: #76daff;
  align-items: center;
  font-size: 0.8em;
  text-decoration: none;
}

.notif-box i {
  position:relative;
  left: 13px;
  background:#fff;
  padding:10px;
  width: 42px;
  height: auto;
  font-size: 20px;
  border-radius: 55%;
  cursor: pointer;
  color:#76daff;
}

.notif-box .fa-xmark {
  position: absolute;
  left: 260px;
  text-align:center;
  background:#76daff;
  color: #fff;
}

.notif-text {
  margin: 25px;
}

.notif-text a {
  text-decoration: none;
  color: #333;
  font-size: 0.9em;
}
.potentiel-chats{
  position: relative;
  display: flex;
  width: 100%;
  height: 70px;
  background: var(--color-black);
  align-items: center;
  font-size: 0.8em;
  text-decoration: none;
  justify-content: center;
  flex-direction: column;
}
.potentiel-chats i {
  top: 50%;
  transform: translateY(-50%);
  height: 20px;
  width: 20px;
  background: #fff;
  border-radius: 50%;
  font-size: 1.2rem;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.23);
  line-height: 20px;
  text-align: center;
  cursor: pointer;
  position: absolute;
  display: flex;
  justify-content: center;
  z-index: 1;
  align-items: center;
}
.potentiel-chats i:first-child {
  left: 10px;
}
.potentiel-chats i:last-child {
  right: 10px;
}
.potentiel-chats .carousel {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: calc((100% / 5) - 12px);
  gap:-10px;
  overflow: hidden;
  transition: all 2ms;
  width: 100%;
}
.potentiel-chats .carousel .avatar{
  cursor: pointer;
}
.search-container {
  position:relative;
  width: 100%;
  height: 40px;
  background:var(--color-light);
   padding:2px 10px;
   border-bottom: 2px solid var(--color-black);
}
.search-container .input{
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  border-radius: 10px;
background:var(--color-black);
  color: var(--color-name);
  padding: 0 5px;
}
.search-container .input input {  
  outline: none;
  border: none;
    background:var(--color-black);
  padding: 0px 10px;
  height: 100%;
  font-size: 12px;
  padding-left: 0px;
  width: 100%;
  margin: 5px
}


.chat-list {
  position: relative;
  height:calc(100% - 170px);
  overflow-y: auto;
}

.chat-list .chat-box {
  position: relative;
  background: var(--color-black);
  width: 100%;
  display:flex;
/*   justify-content: center; */
  align-items:center;
  cursor: pointer;
  padding: 10px;
  border-bottom: 2px solid var(--color-light);
}
.carousel.dragging .mouving{
    cursor: grab;
    user-select: none;
}
.chat-list .chat-box .img-box {
  position:relative;
  width: 55px;
  height:45px;
  overflow:hidden;
  border-radius: 50%;
}
.chat-list .chat-box .img-box img {
  position: absolute;
  width: 100%;
  height: 100%;
}
.chat-list .chat-box .chat-details {
  width: 100%;
  margin-left: 10px;
}

.chat-list .chat-box .chat-details .text-head {
  display:flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom:2px;

}

.chat-list .chat-box .chat-details .text-head h4 {
  font-size: 1.1em;
  font-weight: 600;
  color: var(--color-name);
}

.chat-list .chat-box .chat-details .text-head .time {
  font-size: 0.8em;
  color: #aaa;
}

.chat-list .chat-box .chat-details .text-message {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chat-list .chat-box .chat-details .text-message p {
  color: #aaa;
  font-size: 0.9em;
  overlay: hidden;
}

img {
  width: 100%;
  object-fit: cover;
}

.chat-list .chat-box .chat-details .text-message b {
  background: #06e744;
  color: #fff;
  min-width: 20px;
  height: 20px;
  border-radius: 50%;
/*   text-align: center; */
  font-size: 0.8em;
  font-weight: 400;
  display:flex;
  justify-content:center;
  align-items:center;
}

.chat-list .active {
  background: var(--color-light);
}

.chat-list .chat-box:hover {
  background: var(--color-light);
}

.chat-list .chat-box .chat-details .text-head .unread {
  color: #06e744;
}


/* right-container */


.right-container .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.right-container .header .img-text {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.right-container .header .img-text h4 {
  font-weight: 500;
  line-height: 1.2em;
  margin-left: 15px;
}

.right-container .header .img-text h4 span {
  font-size: 0.8em;
  color: #555;
}

.right-container .header .nav-icons {
  position: relative;
  margin-right:0px;
/*   padding: 5px; */
}

.right-container .header .nav-icons i {
  padding: 10px;
}

.chat-container {
  position:relative;
  width: 100%;
  height: calc(100% - 120px);  /*60+60*/
  padding: 0px 50px;
  overflow-y: auto;
}

.message-box {
  position:relative;
  display: flex;
  width:100%;
  margin: 5px 0;
}
.marksdeliverpending{
  position: absolute;
  bottom: 0;
  right: 0;
}

.message-box p {
  position:relative;
  right: 0;
  text-align: left;
  max-width: 65%;
  padding: 12px;
  background: #dcf8c6;
  border-radius: 10px;
  font-size: 0.9em;
}

.message-box.my-message p::before {
  content : '';
  position: absolute;
  top: 0;
  right: -12px;
  width: 20px;
  height: 20px;
  background: linear-gradient(135deg, #dcf8c6 0%, #dcf8c6 50%, transparent 50%, transparent);
}

.message-box p span {
  display: block;
  margin-top: 5px;
  font-size: 0.8em;
  opacity: 0.5;
}

.my-message {
  justify-content: flex-end;
}

.friend-message p {
  background: #fff;
  justify-content: flex-start;
}

.friend-message {
  justify-content: flex-start;

}

.chat-container .my-message i {
  color: black;
  font-size: 1rem;
}

.message-box.friend-message::before {
  content : '';
  position: absolute;
  top: 0;
  left: -12px;
  width: 20px;
  height: 20px;
  background: linear-gradient(225deg, #fff 0%, #fff 50%, transparent 50%, transparent);
}

.chatbox-input {
  position:relative;
  width: 100%;
  height: 60px;
  background:var(--color-light);
  color: var(--color-name);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.5rem;
}

.chatbox-input i {
  cursor: pointer;
  font-size: 1.8em;
  color: #515855;
}

.chatbox-input i:nth-child(1) {
   margin: 15px;
}

.chatbox-input i:last-child {
  margin-right: 25px;
}

 .chatbox-input input {
    position: relative;
    width: 90%;
    margin: 0 20px;
    padding: 10px 20px;
    border-radius:10px;
    font-size: 1em;
    border:none;
    outline:none;
 }
  .right-container .header .ri-arrow-left-line{
  display: none;
 }

 @media only screen and (max-width: 600px) {
/*  *{
    font-size:small;
  }
  body {
    display: block;
    height: 100%;
    width: 100%;
    background: #ccc;
  }*/
  .main-container {
    position: relative;
    padding: 0;
    margin: 0;
    width: 100%;
    max-width: 100%;
    height: 100vh;
    background: #fff;
    display: flex;
    box-shadow: 0px 1px 1px 0 rgba(0, 0, 0, 0.5),
      0px 2px 5px 0 rgba(0, 0, 0, 0.6);
  }
  .main-container .left-container {
    position: absolute;
    width: 100%;
    height: 100%;
     z-index: 10;
  }
  .main-container .right-container{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 9;
  }
  .main-container .left-container.chat-selected{
    display: none;
  }

  .chat-container{
    padding: 0 1.7rem;
  }
 .right-container .header .ri-arrow-left-line{
  display: block;
 }
 .left-container .header{
     z-index: 10;
  }
   .right-container .header{
    display: flex;
    justify-content: initial;
    width: 100%;
  }
}
